* {
    padding: 0;
    box-sizing: border-box;
    
}

body {
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    text-align: center;
    background:#2a2a2a;
    color: #ffce1f;
    
}



input {
    width: 300px;
    text-align: center;
    padding: 10px;
    font-size: 1rem;
    margin-bottom: 20px;
    border-radius: 10px;
    border: 1px solid #ffce1f;
    background-color: #2a2a2a;
    color: #ffce1f;
    box-shadow: #000000 5px 5px 5px;
}

.meals-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.meal-card {
    flex: 1 0 20%;
    min-width: 180px;
    max-width: 280px;
    border: 2px solid #ffce1f;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: #000000 5px 5px 5px;
}

img {
    width: 90%;
    border-radius: 10px;
}

p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
}